import React from "react";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import style from './welcome.module.css';
import Paramaters from '../tableParamaters/parametersContainer.jsx';
import {Link, NavLink, Route, useHistory} from "react-router-dom";
import {FormAuth} from "../FormsAuth/FormAuth";

const Welcome = (props) => {



    const {push} = useHistory()
    const handleLogin = (email, password) => {
        const auth = getAuth();
        signInWithEmailAndPassword(auth, email, password)
            .then(({user}) => {
                console.log(user.email)
                props.setUser(user.email, user.accessToken, user.uid)
                push('/')
            })
            .catch(console.error)
    }
    return <FormAuth title={"Войти"} handleClick={handleLogin} />

}

export default Welcome;